'use client';

import {
  Container,
  Grid,
  Card,
  Text,
  Avatar,
  Group,
  Stack,
  Badge,
  Button,
  Divider,
  SimpleGrid,
  Paper,
  Title,
  ActionIcon,
  Menu,
  rem,
  ThemeIcon,
} from '@mantine/core';
import {
  IconUser,
  IconSettings,
  IconCreditCard,
  IconWallet,
  IconChartBar,
  IconBell,
  IconDots,
  IconEdit,
  IconLogout,
  IconShield,
  IconHistory,
  IconStar,
} from '@tabler/icons-react';

export default function ProfilePage() {
  return (
    <Container size="xl" py="xl">
      <Grid>
        {/* Левая колонка - Информация о пользователе */}
        <Grid.Col span={{ base: 12, md: 4 }}>
          <Card shadow="sm" padding="lg" radius="md" withBorder>
            <Stack align="center" gap="md">
              <Avatar size={120} radius="xl" color="blue">
                <IconUser size={60} />
              </Avatar>
              
              <Stack align="center" gap="xs">
                <Title order={3}>Иван Петров</Title>
                <Text size="sm" c="dimmed">ivan.petrov@email.com</Text>
                <Badge color="green" variant="light">Активный</Badge>
              </Stack>

              <Divider w="100%" />

              <Group justify="space-between" w="100%">
                <Text size="sm" fw={500}>ID пользователя</Text>
                <Text size="sm" c="dimmed">#12345</Text>
              </Group>

              <Group justify="space-between" w="100%">
                <Text size="sm" fw={500}>Дата регистрации</Text>
                <Text size="sm" c="dimmed">15.01.2024</Text>
              </Group>

              <Group justify="space-between" w="100%">
                <Text size="sm" fw={500}>Последний вход</Text>
                <Text size="sm" c="dimmed">Сегодня, 14:30</Text>
              </Group>

              <Button fullWidth variant="light" leftSection={<IconEdit size={16} />}>
                Редактировать профиль
              </Button>
            </Stack>
          </Card>

          {/* Быстрые действия */}
          <Card shadow="sm" padding="lg" radius="md" withBorder mt="md">
            <Title order={4} mb="md">Быстрые действия</Title>
            <Stack gap="sm">
              <Button variant="subtle" leftSection={<IconCreditCard size={16} />} justify="start">
                Добавить карту
              </Button>
              <Button variant="subtle" leftSection={<IconWallet size={16} />} justify="start">
                Пополнить счет
              </Button>
              <Button variant="subtle" leftSection={<IconShield size={16} />} justify="start">
                Безопасность
              </Button>
              <Button variant="subtle" leftSection={<IconHistory size={16} />} justify="start">
                История операций
              </Button>
            </Stack>
          </Card>
        </Grid.Col>

        {/* Правая колонка - Основная информация */}
        <Grid.Col span={{ base: 12, md: 8 }}>
          {/* Статистика */}
          <SimpleGrid cols={{ base: 1, sm: 2, md: 4 }} mb="xl">
            <Paper p="md" withBorder>
              <Group justify="space-between">
                <div>
                  <Text size="xs" tt="uppercase" fw={700} c="dimmed">Общий баланс</Text>
                  <Text fw={700} size="xl">₽ 125,430</Text>
                </div>
                <ThemeIcon color="blue" variant="light" size="lg">
                  <IconWallet size={20} />
                </ThemeIcon>
              </Group>
            </Paper>

            <Paper p="md" withBorder>
              <Group justify="space-between">
                <div>
                  <Text size="xs" tt="uppercase" fw={700} c="dimmed">Карты</Text>
                  <Text fw={700} size="xl">3</Text>
                </div>
                <ThemeIcon color="green" variant="light" size="lg">
                  <IconCreditCard size={20} />
                </ThemeIcon>
              </Group>
            </Paper>

            <Paper p="md" withBorder>
              <Group justify="space-between">
                <div>
                  <Text size="xs" tt="uppercase" fw={700} c="dimmed">Операции</Text>
                  <Text fw={700} size="xl">47</Text>
                </div>
                <ThemeIcon color="orange" variant="light" size="lg">
                  <IconChartBar size={20} />
                </ThemeIcon>
              </Group>
            </Paper>

            <Paper p="md" withBorder>
              <Group justify="space-between">
                <div>
                  <Text size="xs" tt="uppercase" fw={700} c="dimmed">Рейтинг</Text>
                  <Text fw={700} size="xl">4.8</Text>
                </div>
                <ThemeIcon color="yellow" variant="light" size="lg">
                  <IconStar size={20} />
                </ThemeIcon>
              </Group>
            </Paper>
          </SimpleGrid>

          {/* Последние транзакции */}
          <Card shadow="sm" padding="lg" radius="md" withBorder>
            <Group justify="space-between" mb="md">
              <Title order={4}>Последние транзакции</Title>
              <Button variant="subtle" size="sm">Все операции</Button>
            </Group>

            <Stack gap="sm">
              <Group justify="space-between" p="sm" style={{ backgroundColor: 'var(--mantine-color-gray-0)', borderRadius: 'var(--mantine-radius-sm)' }}>
                <Group gap="sm">
                  <ThemeIcon color="green" variant="light" size="sm">
                    <IconWallet size={16} />
                  </ThemeIcon>
                  <div>
                    <Text size="sm" fw={500}>Пополнение счета</Text>
                    <Text size="xs" c="dimmed">Сегодня, 12:30</Text>
                  </div>
                </Group>
                <Text fw={700} c="green">+₽ 5,000</Text>
              </Group>

              <Group justify="space-between" p="sm">
                <Group gap="sm">
                  <ThemeIcon color="red" variant="light" size="sm">
                    <IconCreditCard size={16} />
                  </ThemeIcon>
                  <div>
                    <Text size="sm" fw={500}>Покупка в магазине</Text>
                    <Text size="xs" c="dimmed">Вчера, 18:45</Text>
                  </div>
                </Group>
                <Text fw={700} c="red">-₽ 1,250</Text>
              </Group>

              <Group justify="space-between" p="sm" style={{ backgroundColor: 'var(--mantine-color-gray-0)', borderRadius: 'var(--mantine-radius-sm)' }}>
                <Group gap="sm">
                  <ThemeIcon color="blue" variant="light" size="sm">
                    <IconWallet size={16} />
                  </ThemeIcon>
                  <div>
                    <Text size="sm" fw={500}>Перевод между счетами</Text>
                    <Text size="xs" c="dimmed">2 дня назад, 09:15</Text>
                  </div>
                </Group>
                <Text fw={700} c="blue">₽ 3,000</Text>
              </Group>
            </Stack>
          </Card>

          {/* Настройки уведомлений */}
          <Card shadow="sm" padding="lg" radius="md" withBorder mt="md">
            <Group justify="space-between" mb="md">
              <Title order={4}>Настройки уведомлений</Title>
              <Menu shadow="md" width={200}>
                <Menu.Target>
                  <ActionIcon variant="subtle">
                    <IconDots size={16} />
                  </ActionIcon>
                </Menu.Target>
                <Menu.Dropdown>
                  <Menu.Item leftSection={<IconSettings size={14} />}>Настройки</Menu.Item>
                  <Menu.Item leftSection={<IconBell size={14} />}>Уведомления</Menu.Item>
                  <Menu.Divider />
                  <Menu.Item leftSection={<IconLogout size={14} />} color="red">Выйти</Menu.Item>
                </Menu.Dropdown>
              </Menu>
            </Group>

            <SimpleGrid cols={{ base: 1, sm: 2 }} spacing="md">
              <Group justify="space-between">
                <div>
                  <Text size="sm" fw={500}>Email уведомления</Text>
                  <Text size="xs" c="dimmed">Получать уведомления на email</Text>
                </div>
                <Button size="xs" variant="light">Включено</Button>
              </Group>

              <Group justify="space-between">
                <div>
                  <Text size="sm" fw={500}>SMS уведомления</Text>
                  <Text size="xs" c="dimmed">Получать SMS о операциях</Text>
                </div>
                <Button size="xs" variant="outline">Выключено</Button>
              </Group>

              <Group justify="space-between">
                <div>
                  <Text size="sm" fw={500}>Push уведомления</Text>
                  <Text size="xs" c="dimmed">Уведомления в приложении</Text>
                </div>
                <Button size="xs" variant="light">Включено</Button>
              </Group>

              <Group justify="space-between">
                <div>
                  <Text size="sm" fw={500}>Безопасность</Text>
                  <Text size="xs" c="dimmed">Уведомления о входе в аккаунт</Text>
                </div>
                <Button size="xs" variant="light">Включено</Button>
              </Group>
            </SimpleGrid>
          </Card>
        </Grid.Col>
      </Grid>
    </Container>
  );
}
